<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">EDIT LISTING</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">OWNER NAME</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.ownerName" :value="listing.ownerName"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">IC</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.ic" :value="listing.ic"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">CONTACT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="listing.contact" :value="listing.contact"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">EMAIL</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.email" :value="listing.email"></fg-input>
            </div>
          </div>
          <hr>
          <div slot="header">
            <h5 class="title">EDIT LISTING DETAIL</h5>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">BLOCK NO</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.blockno" :value="listing.blockno"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">SQFT</label>
            <div class="col-md-2">
              <fg-input type="text" v-model="listing.sqft" :value="listing.sqft"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">RENTAL FEE</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="listing.rentalFees" :value="listing.rentalFees"></fg-input>
            </div>
            <label class="col-md-2 col-form-label text-right">AVAILABILITY</label>
            <div class="col-md-3">
              <!-- <n-switch v-model="switches.defaultOn" min-width="120" on-text="OCCUPIED" off-text="EMPTY"></n-switch> -->
              <toggle-button
                class="switch"
                :sync="true"
                :value="false"
                :width="90"
                :color="{checked: '#7DCE94', unchecked: '#BE3D62'}"
                :labels="{checked: 'EMPTY', unchecked: 'OCCUPIED'}"
                v-model="listing.availability"
              />
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-1 text-right">MANAGEMENT FEE</label>
            <div class="col-md-3">
              <fg-input
                type="text"
                v-model="listing.managementFees"
                :value="listing.managementFees"
              ></fg-input>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 offset-md-4">
              <n-button
                class="button"
                type="info"
                @click.native="toggle_status(listing.availability); put(); handleUpdate();"
              >UPDATE</n-button>
              <a class="cancel">Cancel</a>
              <!-- <n-button class="button" type="danger">CANCEL</n-button> -->
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import { Radio } from "src/components";
import { Switch } from "src/components";
import swal from "sweetalert2";

export default {
  components: {
    [Radio.name]: Radio,
    [Switch.name]: Switch
  },
  data() {
    return {
      id: this.$route.params.id,
      listing: {
        ownerName: "",
        ic: "",
        contact: "",
        email: "",
        blockno: "",
        sqft: "",
        rentalFees: "",
        managementFees: "",
        availability: false,
        status: ""
      },
      switches: {
        defaultOn: true
      },
      key_data: {
        ownerName: "",
        ic: "",
        contact: "",
        email: "",
        blockno: "",
        sqft: "",
        rentalFees: "",
        managementFees: "",
        availability: false,
        status: ""
      }
    };
  },
  methods: {
    //update the data to database
    put() {
      this.$http.put(
        "https://admindashboard-b2ab9.firebaseio.com/listing/" +
          this.id +
          ".json",
        this.listing
      );
    },
    toggle_status(et) {
      if (et == false) {
        this.listing.status = "OCCUPIED";
      } else {
        this.listing.status = "EMPTY";
      }
    },
    handleUpdate() {
      swal({
        title: `You updated ${this.listing.ownerName}`,
        buttonsStyling: false,
        type: "success",
        confirmButtonClass: "btn btn-success btn-fill"
      });
    }
  },
  created() {
    //get data to show in textboxes
    this.$http
      .get("https://admindashboard-b2ab9.firebaseio.com/listing.json")
      .then(function(data) {
        return data.json();
      })
      .then(function(data) {
        //pass data to key_data object
        this.key_data = data;
        //set the fields accordingly with the unique id
        this.listing.ownerName = this.key_data[this.id].ownerName;
        this.listing.ic = this.key_data[this.id].ic;
        this.listing.contact = this.key_data[this.id].contact;
        this.listing.email = this.key_data[this.id].email;
        this.listing.blockno = this.key_data[this.id].blockno;
        this.listing.sqft = this.key_data[this.id].sqft;
        this.listing.rentalFees = this.key_data[this.id].rentalFees;
        this.listing.availability = this.key_data[this.id].availability;
        this.listing.managementFees = this.key_data[this.id].managementFees;

        //toggle radio button
        if (this.listing.availability == true) {
          this.listing.status = "EMPTY";
        } else {
          this.listing.status = "OCCUPIED";
        }
      });
  }
};
</script>
<style scoped>
.display-inline {
  display: inline !important;
}
.cancel {
  /* display: inline; */
  margin-left: 20px;
  color: red;
  text-decoration: underline !important;
}

.switch {
  margin-top: 5px;
}
</style>
